@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300&family=Roboto:wght@100;300&display=swap);
html{scroll-behavior:smooth}.main-wrapper{position:absolute;top:0px;right:0px;bottom:0px;left:0px}.title{font-family:'Lato';font-weight:300;margin-left:50px;margin-right:50px}.title-normal{font-style:normal}.title-italic{font-style:italic}.title-l{font-size:60px}.title-m{font-size:40px}.title-s{font-size:25px}.title-link{color:white;text-decoration:none;transition-duration:.5s}.title-link:hover{color:grey;transition-duration:.5s}.paragraph{font-family:'Roboto';font-weight:300;font-size:18px;margin-left:50px;margin-right:50px;margin-top:0px;margin-bottom:0px}.spacer{margin-left:40px;margin-right:40px}.empty-l{margin-top:50px !important}.empty-m{margin-top:40px !important}.empty-s{margin-top:30px !important}.white-text{color:white}.black-text{color:black}.purple-text{color:#8B8599}.embed-container{--video--width: 1296;--video--height: 540;position:relative;padding-bottom:calc(var(--video--height) / var(--video--width) * 100%);overflow:hidden;max-width:100%}.embed-container iframe,.embed-container object,.embed-container embed{position:absolute;top:0;left:0;width:100%;height:100%}.navigation-link{margin-top:15px;margin-left:20px;transition-duration:.5s}.navigation-link:hover{color:grey;transition-duration:.5s}

.home-row-wrapper{height:100%;max-width:100%;margin:0px !important}.home-title-wrapper{color:black;background-color:white;display:flex;justify-content:center;align-items:center}.home-description-wrapper{color:white;background-color:#8DA1B9;display:flex;justify-content:center;align-items:center}

.about-row-wrapper{height:30%;display:flex;flex-direction:column;max-width:100%;margin:0px !important}.about-description-row-wrapper{display:flex;flex-direction:column;max-width:100%;margin:0px 25px 0px 25px !important}.about-title-wrapper{color:white;background-color:#CBB3BF;display:flex;justify-content:center;align-items:center}.about-description-wrapper{color:black;background-color:white;display:flex;justify-content:center;align-items:center}

.works-row-wrapper{height:100%;max-width:100%;margin:0px !important}.works-title-wrapper{color:white;background-color:#95ADB6}.works-description-wrapper{color:black;background-color:white}.arrow-link{margin-top:15px;margin-left:20px;transition-duration:.5s;display:none}.arrow-link:hover{color:#CBB3BF;transition-duration:.5s}@media (max-width: 990px){.arrow-link{display:block}}

.contact-row-wrapper{height:30%;display:flex;flex-direction:column;max-width:100%;margin:0px !important}.contact-description-row-wrapper{display:flex;flex-direction:column;max-width:100%;margin:0px 25px 0px 25px !important}.contact-title-wrapper{color:white;background-color:#8B8599;display:flex;justify-content:center;align-items:center}.contact-description-wrapper{color:black;background-color:white;display:flex;justify-content:center;align-items:center;flex-direction:column}.contact-link-wrapper{margin-top:25px !important}.contact-link{margin-right:10px;margin-left:10px;color:black;transition-duration:.5s}.contact-link:hover{color:#95ADB6;transition-duration:.5s}

.notFound-row-wrapper{height:100%;max-width:100%;margin:0px !important}.notFound-title-wrapper{color:white;background-color:#7E8D85;display:flex;justify-content:center;align-items:center}

